    "qu es un plazo fijo": "Un plazo fijo es un instrumento financiero que consiste en depositar dinero en una entidad financiera por un perodo determinado a cambio de una tasa de inters fija. Al finalizar el plazo, recibes tu capital ms los intereses generados.",
    "qu es el riesgo": "En finanzas, el riesgo es la posibilidad de que el rendimiento real de una inversin sea diferente al esperado, especialmente la posibilidad de perder parte o la totalidad del capital invertido. Generalmente, a mayor riesgo, mayor rendimiento potencial.",
    "qu es un bono": "Un bono es un instrumento de deuda emitido por gobiernos, municipalidades o empresas para financiar proyectos o actividades. Al comprar un bono, ests prestando dinero al emisor a cambio de pagos peridicos de inters y la devolucin del principal al vencimiento.",
